<template>
    <section class="articleDetailsComponent">
        <div class="articleDetails-box">

            <div class="articleDetails-content">

                <!---->
                <img @click="close" class="articleDetails-close" src="../../../assets/images/article/close.png" alt="">

                <!---->
                <div class="articleDetails-content-header">
                    <p>
                        ({{articleStatus[getContentInfo.status]}}){{getContentInfo.title}}
                        <button
                                class="tag-read"
                                :data-clipboard-text="getContentInfo.title"
                                @click="copyTitle">
                        </button>
                    </p>
                    <p><span>{{getContentInfo.date}}</span></p>
                    <p><span>{{getContentInfo.editor}}</span></p>
                    <p><span>{{getContentInfo.key_word}}</span></p>
                    <p><a :href="getContentInfo.url" target="_blank">{{getContentInfo.url}}</a></p>
                    <p><span>{{getContentInfo.source}}</span></p>
                    <p><span>{{getContentInfo.target_name}}</span></p>
                </div>

                <!-- 3  2  4  -->
                <div class="articleDetails-content-operating">
                    <input v-if="menuLeftIndex === 2" type="button" @click="openArticleSet(3)" value=""
                           :disabled="+getContentInfo.status === 1 || +getContentInfo.status === 3 || +getContentInfo.status === 4"
                           :class="+getContentInfo.status === 1 || +getContentInfo.status === 3 || +getContentInfo.status === 4 ? 'inputDisabled' : ''">

                    <input type="button" value="" @click="prop()">
                    <input type="button" value="" @click="next()">

                    <div style="float: right">
                        <!--<input type="button" value="">-->

                        <input v-if="menuLeftIndex === 1" type="button" @click="openArticleSet(2)" value=""
                               :disabled="+getContentInfo.status === 2 || +getContentInfo.status === 3"
                               :class="+getContentInfo.status === 2 || +getContentInfo.status === 3 ? 'inputDisabled' : ''">
                        <input v-if="menuLeftIndex === 1" type="button" class="articleDetails-content-operating-forbid"
                               @click="openArticleSet(4)"
                               value=""
                               :disabled="+getContentInfo.status === 4"
                               :class="+getContentInfo.status === 4 ? 'inputDisabled' : ''">
                    </div>
                </div>

                <!---->
                <div class="forbidMsg animated fadeIn" v-if="isForbidMsg">
                    <input type="text" placeholder="" v-model="forbidMsg">
                    <button @click="setForbidMsg"></button>
                </div>

                <!---->
                <div class="articleDetails-content-message" id="articleDetails-content-message">
                    <div class="articleDetails-content-message-border">
                        <div v-for="(item,index) in getContentInfo.content" :key="index">
                            <br/>
                            <img v-if="getRegx(item)" :src="item" alt="">
                            <p v-else>{{item}}</p>
                        </div>
                    </div>
                </div>

                <Spin size="large" fix v-if="spinShow"></Spin>

                <!---->
                <Modal v-model="modal"
                       title=""
                       @on-ok="asyncOK">
                    <p>{{modalMsg}}</p>
                </Modal>

            </div>
        </div>
    </section>
</template>

<script>
    import article from './article'
    import Clipboard from 'clipboard'

    export default {
        name: "articleDetailsComponent",
        props: {
            /**
             *  ID
             * */
            id: {
                type: Number
            },
            /**
             * 
             * */
            tableIndex: {
                type: Number
            },
            /**
             * 
             * */
            tableList: {
                type: Array
            },
            /**
             * 
             * 1  2 
             * */
            menuLeftIndex: {
                type: Number
            }
        },
        mixins: [article],
        data() {
            return {
                index: this.tableIndex, // 
                articleStatus: {
                    '1': '',
                    '2': '',
                    '3': '',
                    '4': ''
                },
                spinShow: true, //  loading 
                modal: false, // 
                modalMsg: '', // modal 
                operatingBtn: '', // 
                isForbidMsg: false,//  
                forbidMsg: '', // 
            }
        },
        async created() {
            const that = this;
            /**
             * 
             * */
            await that.getArticleDetails();
            /**
             * 
             * */
            await that._isJurisdiction();
        },
        methods: {
            /**
             * 
             * */
            copyText() {
            },
            /**
             * 
             * */
            copyTitle() {
                const clipboard = new Clipboard('.tag-read');
                clipboard.on('success', () => {
                    alert('');
                    // 
                    clipboard.destroy()
                });
                clipboard.on('error', () => {
                    // 
                    console.log('');
                    // 
                    clipboard.destroy();
                })
            },
            /**
             * 
             * */
            close() {
                const that = this;
                that.$emit('close')
            },
            /**
             * 
             * */
            setForbidMsg() {
                const that = this;
                if (that.forbidMsg !== '') {
                    that.setArticleStatus(that.id, 4, that.forbidMsg);
                }
            },
            /**
             * 
             * */
            next() {
                const that = this;
                if (that.index + 1 >= that.tableList.length) {
                    return
                }
                that.spinShow = true;
                that.goNextArticleDetails()
            },
            /**
             * 
             * */
            prop() {
                const that = this;
                if (that.index <= 0) {
                    return
                }
                that.spinShow = true;
                that.goPropArticleDetails();
            },
            /**
             * 
             * */
            openArticleSet(type) {
                const that = this;

                switch (type) {
                    case 2:
                        that.modalMsg = '';
                        that.modal = true;
                        that.operatingBtn = type;
                        break;
                    case 3:
                        that.modalMsg = '';
                        that.modal = true;
                        that.operatingBtn = type;
                        break;
                    case 4:
                        that.isForbidMsg = !that.isForbidMsg;
                        break;
                    default:
                }
            },
            /**
             * 
             * */
            asyncOK() {
                const that = this;
                that.modal = false;
                switch (that.operatingBtn) {
                    case 2:
                        that.setArticleStatus(that.id, that.operatingBtn, '');
                        break;
                    case 3:
                        that.setArticleStatus(that.id, that.operatingBtn, '');
                        break;
                    default:
                }
            },
            /**
             *  
             * */
            getRegx(str) {
                const reg = /^(f|ht){1}(tp|tps):\/\/([\w-]+\.)+[\w-]+(\/[\w- ./?%&=]*)?/;
                return reg.test(str);
            }
        }
    }
</script>

