<template>
    <section class="statusComponent">

        <!---->
        <div class="status-btn">
            <button
                    :class="statusIndex === 0 ? 'status-btn-avtive' : ''"
                    @click="selectStatus(0,menuLeftIndex)"
            >
                ({{getDisplayCountInfo}})
            </button>
            <button
                    :class="statusIndex === 1 ? 'status-btn-avtive' : ''"
                    v-if="menuLeftIndex !== 2"
                    @click="selectStatus(1,menuLeftIndex)"
            >
                ({{waitView}})
            </button>
            <button
                    :class="statusIndex === 2 ? 'status-btn-avtive' : ''"
                    @click="selectStatus(2,menuLeftIndex)"
            >
                ({{waitUsed}})
            </button>
            <button
                    :class="statusIndex === 3 ? 'status-btn-avtive' : ''"
                    @click="selectStatus(3,menuLeftIndex)"
            >
                ({{used}})
            </button>
            <button
                    :class="statusIndex === 4 ? 'status-btn-avtive' : ''"
                    v-if="menuLeftIndex !== 2"
                    @click="selectStatus(4,menuLeftIndex)"
            >
                ({{forbid}})
            </button>
        </div>

        <Spin size="large" fix v-if="spinShow"></Spin>

    </section>
</template>

<script>
    import status from './status.js'

    export default {
        name: "statusComponent",
        props: {
            /**
             * 
             * 1  2 
             * */
            menuLeftIndex: {
                type: Number
            }
        },
        mixins: [status],
        data() {
            return {
                spinShow: true,
                statusIndex: 0, // 
                waitView: '', // 
                waitUsed: '', // 
                used: '', // 
                forbid: '', // 
            }
        },
        created() {
            const that = this;
            /**
             * --
             * */
            that.statusInit();
        },
        methods: {
            /**
             * 
             * 
             * */
            selectStatus(index, menuleftIndex) {
                const that = this;
                that.statusIndex = index;
                that.$emit('selectStatus', {'index': index, 'form': menuleftIndex});
            }
        }
    }
</script>

<style lang="less">
    #boxSizing {
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
    }

    .statusComponent {
        margin-bottom: 10px;
        padding: 10px;

        #boxSizing;
        .status-btn {
            button {
                #boxSizing;
                padding: 10px 20px;
                font-size: 18px;
                -webkit-border-radius: 5px;
                -moz-border-radius: 5px;
                border-radius: 5px;
                background-color: #f8f8f9;
                border: 1px solid #f8f8f9;
                margin-right: 20px;
                color: #000;
                text-align: center;
                cursor: pointer;
            }
            .status-btn-avtive {
                background-color: #2d8cf0;
                border: 1px solid #2d8cf0;
                color: #fff;
            }
        }
    }
</style>