<template>
    <section class="filterComponent">

        <!---->
        <div class="filter-btn" v-if="filterIsShow">

            <button
                    v-for="(item,index) in [...[{'key_word': '','count':calculation(getKeyWordCountInfo)}],...getKeyWordCountInfo]"
                    :key="index"
                    :class="filterIndex === index ? 'filter-btn-avtive' : ''"
                    @click="selectFilter(item.key_word,index)">
                {{item.key_word}}({{item.count}})
            </button>

        </div>

        <Spin size="large" fix v-if="spinShow"></Spin>

    </section>
</template>

<script>

    import filter from './filter'

    export default {
        name: "filterComponent",
        props: {
            statusJson: {
                type: Object
            }
        },
        data() {
            return {
                spinShow: true,
                filterIndex: 0, // 
                filterIsShow: false //  calculation 
            }
        },
        mixins: [filter],
        async created() {
            const that = this;
            /**
             * 
             * */
            await that.getCrucialNumber('');
            that.spinShow = false;
            that.filterIsShow = true;
        },
        methods: {
            /**
             * 
             *  table 
             * */
            selectFilter(type, index) {
                const that = this;
                that.filterIndex = index;
                that.$emit('selectFilter', {'filterType': type});
            },
            /**
             * 
             * */
            calculation(o) {
                let sum = 0;
                o.forEach((item) => {
                    sum += parseInt(item.count);
                });
                return sum;
            }
        },
        watch: {
            /**
             * 
             * */
            statusJson: {
                async handler() {
                    const that = this;
                    that.spinShow = true;
                    that.filterIndex = 0; // ,
                    if (that.statusJson.form === 1 && that.statusJson.index === 0) {
                        await that.getCrucialNumber('');
                        that.spinShow = false;
                    } else if (that.statusJson.form === 2 && that.statusJson.index === 0) {
                        await that.getCrucialNumber('');
                        that.spinShow = false;
                    } else {
                        await that.getCrucialNumber(that.statusJson.index);
                        that.spinShow = false;
                    }
                },
                deep: true,
                immediate: false,
            }
        }
    }
</script>

<style lang="less">
    #boxSizing {
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
    }

    .filterComponent {
        margin-bottom: 10px;
        padding: 10px;

        #boxSizing;
        .filter-btn {
            button {
                #boxSizing;
                padding: 10px 20px;
                font-size: 18px;
                -webkit-border-radius: 5px;
                -moz-border-radius: 5px;
                border-radius: 5px;
                background-color: #f8f8f9;
                border: 1px solid #f8f8f9;
                margin-right: 20px;
                color: #000;
                text-align: center;
                cursor: pointer;
            }
            .filter-btn-avtive {
                background-color: #2d8cf0;
                border: 1px solid #2d8cf0;
                color: #fff;
            }
        }
    }
</style>