<template>
    <section class="tableComponent">

        <!---->
        <div class="table">
            <table>
                <tr>
                    <th v-for="(item,index) in tableHeaderList" :key="index">{{item.tableHeader}}</th>
                </tr>
                <tr v-for="(item,index) in tableList" :key="index">
                    <td>{{item.id}}</td>
                    <td>{{item.release_time === 'None' ? 'null' : item.release_time}}</td>
                    <td @click="articleDetails(item.id,index)" class="tableTitle">{{item.title}}</td>
                    <td>{{item.source}}</td>
                    <td>{{item.key_word}}</td>
                    <td>{{item.target_name}}</td>
                    <td>{{articleStatus[item.status]}}</td>
                </tr>
            </table>
            <Spin size="large" fix v-if="spinShow"></Spin>
        </div>

        <!---->
        <button class="table-more" @click="getMore"></button>

        <!---->
        <c-articleDetails
                :id="articleId"
                :tableIndex="tableIndex"
                :tableList="tableList"
                :menuLeftIndex="menuLeftIndex"
                v-if="isShow"
                @close="listerClose">
        </c-articleDetails>

    </section>
</template>

<script>
    import table from './table'
    import cArticleDetails from '../_articleDetails/articleDetailsComponent'

    export default {
        name: "tableComponent",
        data() {
            return {
                isShow: false,
                /**
                 * 
                 * */
                articleStatus: {
                    '1': '',
                    '2': '',
                    '3': '',
                    '4': ''
                },
                /**
                 * _table-
                 * */
                tableHeaderList: [
                    {'tableHeader': 'ID'},
                    {'tableHeader': ''},
                    {'tableHeader': ''},
                    {'tableHeader': ''},
                    {'tableHeader': ''},
                    {'tableHeader': ''},
                    {'tableHeader': ''},
                ],
                spinShow: true, // loading
                start: 0, // 
                count: 40, // 
                tableList: null, // 
                articleId: null, // ID
                tableIndex: 0, //  
            }
        },
        props: {
            /**
             * 
             * */
            statusIndex: {
                type: Number
            },
            /**
             * 
             * */
            filterType: {
                type: Object
            },
            /**
             * 
             * 1  2 
             * */
            menuLeftIndex: {
                type: Number
            }
        },
        mixins: [table],
        components: {cArticleDetails},
        async created() {
            const that = this;
            /**
             * table
             * */
            await that.getAllTableList(that.start, that.count, '', '', '');
            that.spinShow = false;
        },
        methods: {
            /**
             * 
             *   
             * */
            getMore() {
                const that = this;

                switch (that.statusIndex) {
                    case 0:
                        that.start += 40;
                        that._typeOf(that.start, that.count, that.statusIndex, '', that.filterType.filterType);
                        break;
                    case 1:
                        that.start += 40;
                        that._typeOf(that.start, that.count, that.statusIndex, '', that.filterType.filterType);
                        break;
                    case 2:
                        that.start += 40;
                        that._typeOf(that.start, that.count, that.statusIndex, '', that.filterType.filterType);
                        break;
                    case 3:
                        that.start += 40;
                        that._typeOf(that.start, that.count, that.statusIndex, '', that.filterType.filterType);
                        break;
                    case 4:
                        that.start += 40;
                        that._typeOf(that.start, that.count, that.statusIndex, '', that.filterType.filterType);
                        break;
                    default:
                }
            },
            _typeOf(start, count, status, sort, filterType) {
                const that = this;
                if (filterType === '') {
                    if (status === 0) {
                        that.getMoreTableData(start, count, '', sort, '')
                    } else {
                        that.getMoreTableData(start, count, status, sort, '')
                    }
                }
                if (filterType === '') {
                    if (status === 0) {
                        that.getMoreTableData(start, count, '', '', filterType)
                    } else {
                        that.getMoreTableData(start, count, status, sort, filterType)
                    }
                }
                if (filterType === '') {
                    if (status === 0) {
                        that.getMoreTableData(start, count, '', '', filterType)
                    } else {
                        that.getMoreTableData(start, count, status, sort, filterType)
                    }
                }
                if (filterType === '-') {
                    if (status === 0) {
                        that.getMoreTableData(start, count, '', '', filterType)
                    } else {
                        that.getMoreTableData(start, count, status, sort, filterType)
                    }
                }
            },
            /**
             * 
             * */
            articleDetails(articleId, tableIndex) {
                const that = this;
                that.tableIndex = tableIndex;
                that.articleId = +articleId;
                that.isShow = true;
            },
            /**
             * 
             * */
            listerClose() {
                const that = this;
                that.isShow = false;
                that.$emit('close')
            }
        },
        watch: {
            /**
             * 
             * 0  1  2  3  4 
             * */
            statusIndex: {
                async handler() {
                    const that = this;
                    that.start = 0; //  
                    that.count = 40; //  
                    that.filter = []; //   
                    that.spinShow = true;
                    if (that.statusIndex === 0) {
                        await that.getAllTableList(that.start, that.count, '', '', '');
                    }
                    if (that.statusIndex === 1) {
                        await that.getAllTableList(that.start, that.count, that.statusIndex, '', '');
                    }
                    if (that.statusIndex === 2) {
                        await that.getAllTableList(that.start, that.count, that.statusIndex, '', '');
                    }
                    if (that.statusIndex === 3) {
                        await that.getAllTableList(that.start, that.count, that.statusIndex, '', '');
                    }
                    if (that.statusIndex === 4) {
                        await that.getAllTableList(that.start, that.count, that.statusIndex, '', '');
                    }
                    that.spinShow = false;
                },
                deep: true,
                immediate: false
            },
            /**
             * 
             * ''  
             * */
            filterType: {
                async handler() {
                    const that = this;
                    that.spinShow = true;
                    that.start = 0; //  
                    that.count = 40; //  

                    if (that.statusIndex === 0) {
                        if (that.filterType.filterType === '') {
                            await that.getAllTableList(that.start, that.count, '', '', '');
                        } else {
                            await that.getAllTableList(that.start, that.count, '', '', that.filterType.filterType);
                        }
                    }
                    if (that.statusIndex === 1) {
                        if (that.filterType.filterType === '') {
                            await that.getAllTableList(that.start, that.count, that.statusIndex, '', '');
                        } else {
                            await that.getAllTableList(that.start, that.count, that.statusIndex, '', that.filterType.filterType);
                        }
                    }
                    if (that.statusIndex === 2) {
                        if (that.filterType.filterType === '') {
                            await that.getAllTableList(that.start, that.count, that.statusIndex, '', '');
                        } else {
                            await that.getAllTableList(that.start, that.count, that.statusIndex, '', that.filterType.filterType);
                        }
                    }
                    if (that.statusIndex === 3) {
                        if (that.filterType.filterType === '') {
                            await that.getAllTableList(that.start, that.count, that.statusIndex, '', '');
                        } else {
                            await that.getAllTableList(that.start, that.count, that.statusIndex, '', that.filterType.filterType);
                        }
                    }
                    if (that.statusIndex === 4) {
                        if (that.filterType.filterType === '') {
                            await that.getAllTableList(that.start, that.count, that.statusIndex, '', '');
                        } else {
                            await that.getAllTableList(that.start, that.count, that.statusIndex, '', that.filterType.filterType);
                        }
                    }
                    that.spinShow = false;
                },
                deep: true,
                immediate: false
            }
        }
    }
</script>

<style lang="less">
    #boxSizing {
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
    }

    .tableComponent {
        #boxSizing;

        .table {
            position: relative;
            table {
                width: 100%;
                text-align: center;
                .tableTitle {
                    cursor: pointer;
                    text-decoration: underline;
                    -webkit-transition: all .5s;
                    -moz-transition: all .5s;
                    -ms-transition: all .5s;
                    -o-transition: all .5s;
                    transition: all .5s;

                    &:hover {
                        background-color: #2d8cf0;
                        color: #fff;
                    }
                }
            }
            table, tr, th, td {
                border: 1px solid #dcdee2;
                padding: 20px;
            }

            th {
                font-size: 20px;
                font-weight: bold;
                color: #17233d;
            }
            margin-bottom: 20px;
        }
        .table-more {
            padding: 10px 20px;
            font-size: 18px;
            -webkit-border-radius: 5px;
            -moz-border-radius: 5px;
            border-radius: 5px;
            background-color: #2d8cf0;
            border: 1px solid #2d8cf0;
            color: #fff;
            text-align: center;
            cursor: pointer;
        }
    }
</style>