import * as config from '../../../../unit/config'
import {mapGetters} from 'vuex'

export default {
    data() {
        return {}
    },
    methods: {
        /**
         * 
         * ''  1  2  3  4 
         * */
        getAllTableList(start, count, status, sort, crucial) {
            const that = this;
            that.$store.dispatch('_getCrawlerData', {
                im: config.requestInterface.getCrawlerData,
                fps: {
                    'start': start,
                    'count': count,
                    'status': status,
                    'sort': sort,
                    'key_word': crucial
                },
                url: config.CONST_FETCH_REQUEST_URI
            }).then(res => {
                if (res.result === 'success') {
                    that.tableList = res.back_value;
                }
            });
        },
        /**
         * 
         * ''  1  2  3  4 
         * */
        getMoreTableData(start, count, status, sort, crucial) {
            const that = this;
            that.$store.dispatch('_getCrawlerData', {
                im: config.requestInterface.getCrawlerData,
                fps: {
                    'start': start,
                    'count': count,
                    'status': status,
                    'sort': sort,
                    'key_word': crucial
                },
                url: config.CONST_FETCH_REQUEST_URI
            }).then(res => {
                if (res.result === 'success') {
                    // 
                    if (res.back_value.length === 0) {
                        this.$Message['error']({
                            background: true,
                            content: ''
                        });
                        that.spinShow = false;
                    } else {
                        // 
                        that.tableList = [...that.tableList, ...res.back_value];
                        that.spinShow = false;
                    }
                }
            });
        }
    },
    computed: {
        ...
            mapGetters([
                'getCrawlerDataInfo',
                'getKeyWordCountInfo'
            ])
    }
}
;